 * @Description: 
-->
<template>
  <div class="items" :style="{ paddingBottom: showBtn ? '10px' : '19px' }">
    <div class="left">
      <div class="icon">
        <img :src="src" alt="">
      </div>
      <div class="main">
        <div class="up">
          <template v-if="type == 1">
            <div class="num">{{ add == '+' ? '-' : '+' }}{{ list.usdt }}</div>
            <div class="unit">USDT</div>
            <slot name="tage" />
          </template>
          <template v-if="type == 2">
            <div class="name">{{ list && list.action | formart }}</div>
            <!-- <div class="name" style="margin-left: 12px;">火箭</div> -->
          </template>
        </div>
        <div class="down">{{ list && list.addtime }}</div>
      </div>
    </div>
    <div class="rig">
      <div class="money">
        <div class="blac">{{ add }}{{ list && list.totalcoin }}</div>
        <slot name="unit" />
      </div>
      <template v-if="showBtn">
        <slot name="btn" />
      </template>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    GoldCoinRecharge: {
      type: Object
    },
    GoldCoinItem: {
      type: Object
    },
    profitItem: {
      type: Object
    },
    WithdrawalItem: {
      type: Object
    },
    type: {
      type: Number,
      default: 1
    },
    src: {
      type: String
    },
    showBtn: {
      type: Boolean,
      default: false
    },
    add: {
      type: String,
      default: '+'
    }
  },
  data () {
    return {
      list: {}
    }
  },
  mounted () {
    if (this.GoldCoinItem) {
      this.list = Object.assign(this.GoldCoinItem)
    }
    if (this.GoldCoinRecharge) {
      let item = {
        usdt: this.GoldCoinRecharge.money,
        addtime: this.GoldCoinRecharge.addtime,
        totalcoin: this.GoldCoinRecharge.coin
      }
      this.list = Object.assign(item)
    }
    if (this.profitItem) {
      let item = {
        action: this.profitItem.action,
        addtime: this.profitItem.addtime,
        totalcoin: this.profitItem.votes
      }
      this.list = Object.assign(item)
    }
    if (this.WithdrawalItem) {
      let item = {
        usdt: this.WithdrawalItem.money,
        addtime: this.WithdrawalItem.addtime,
        totalcoin: this.WithdrawalItem.votes
      }
      this.list = Object.assign(item)
    }
  },
  filters: {
    formart (val) {
      switch (val) {
        case 'buyvip':
          return '购买vip'
          break;
        case 'watchvideo':
          return '观看收费视频'
          break;
        case 'uppop':
          return '视频上热门'
          break;
        case 'pop_refund':
          return '上热门视频指定时间内没达到播放量退费'
          break;
        case 'sendgift':
          return '直播间送礼物'
          break;
        case 'video_sendgift':
          return '视频送礼物'
          break;
        case 'yurntable_game':
          return '转盘游戏'
          break;
        case 'turntable_wins':
          return '转盘中奖'
          break;
        case 'open_guard':
          return '开通守护'
          break;
        case 'daily_tasks':
          return '每日任务'
          break;
        case 'reg_reward':
          return '注册奖励'
          break;
        case 'signin_reward':
          return '签到奖励'
          break;
        case 1:
          return '邀请'
          break;
        case 2:
          return '观看视频'
          break;
        case 3:
          return '收费视频收入'
          break;
        case 4:
          return '视频送礼物'
          break;
        case 5:
          return '直播间送礼物'
          break;
        case 6:
          return '开通守护'
          break;
        default: ''
      }
    }
  }
}
</script>
<style lang="less" scoped>
div {
  line-height: 1;
}

.items {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  height: 76px;
  padding: 8px 10px 19px 6px;
  box-sizing: border-box;
  background: #FFFFFF;
  border-radius: 10px;
  margin-bottom: 5px;

  .left {
    display: flex;
    align-self: start;
    flex: 1;
    width: 0;

    .icon {
      width: 36px;
      height: 36px;
      margin-right: 13px;
      flex-shrink: 0;
    }

    .main {
      display: flex;
      flex-direction: column;
      margin-top: 11px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;

      .down {
        font-size: 12px;
        font-weight: 500;
        color: #B1B5C5;
      }

      .up {
        display: flex;
        align-items: center;
        margin-bottom: 17px;

        .name {
          font-size: 14px;
          font-weight: 400;
          color: #333333;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }

        .num {
          font-size: 15px;
          font-weight: bold;
          margin-right: 7px;
          color: #333;
        }

        .unit {
          font-size: 14px;
          font-weight: 500;
          color: #B1B5C5;
          margin-right: 12px;
        }
      }
    }
  }

  .rig {
    display: flex;
    flex-direction: column;

    .money {
      display: flex;
      align-items: center;
      margin-top: 8px;

      .blac {
        font-size: 15px;
        font-weight: bold;
        color: #1577FF;
        margin-right: 7px;
      }
    }
  }
}
</style>