<template>
  <div class="gold">
    <van-tabs @change="tabChange" v-model="active" :before-change="beforeChange">
      <van-tab title="收益明细">
        <van-list v-model="loading" :immediate-check="false" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <item add="+" :type="2" v-for="(item, index) in list1" :key="index" :profitItem="item"
            :src="require('@/assets/gold.png')">
            <template #unit>
              <div class="unit">星票</div>
            </template>
          </item>
        </van-list>
      </van-tab>
      <van-tab title="提现明细">
        <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <item add="-" :showBtn="true" :type="1" v-for="(item, index) in list2" :WithdrawalItem="item" :key="index"
            :src="require('@/assets/gold.png')">
            <template #unit>
              <div class="unit">星票</div>
            </template>
            <template #btn>
              <div class="btn" v-if="item.status == 0">未处理</div>
              <div class="btn fail" v-else-if="item.status == 1">提现成功</div>
              <div class="btn success" v-else-if="item.status == 2">拒绝提现</div>
            </template>
          </item>
        </van-list>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import { queryUserEarningsDetails, queryTheWithdrawalList } from '@/api/request.js'
import { Toast, Dialog } from 'vant';
import { callhandler } from "@/util/bridge";
import item from './item'
export default {
  components: {
    item
  },
  data () {
    return {
      active: 0,
      status: 0,
      list1: [],
      list2: [],
      loading: false,
      finished: false,
      pageNumber1: 1,
      pageNumber2: 1
    }
  },
  mounted () {
    this.onLoad()
  },
  methods: {
    tabChange (val) {
      this.finished = false
      this.status = val
      this.pageNumber2 = this.pageNumber1 = 1
      this.onLoad()
    },
    beforeChange () {
      // 返回 false 表示阻止此次切换
      return !this.loading
    },
    onLoad () {
      this.loading = true
      setTimeout(async () => {
        let headerData = await callhandler("getUserToken")
        headerData = {
          token: headerData.split(',')[0],
          userId: headerData.split(',')[1]
        }
        let page = this.status ? this.pageNumber2 : this.pageNumber1
        let res = ''
        let parms = {
          pageNumber: page,
          pageSize: 10
        }
        if (page == 1) this.list2 = this.list1 = [];
        console.log(parms, this.status)
        res = await (this.status ? queryTheWithdrawalList : queryUserEarningsDetails)(parms, headerData)
        this.loading = false;
        if (!res) {
          this.finished = true
          return
        }
        this.status ? this.list2 = this.list2.concat(res.data) : this.list1 = this.list1.concat(res.data)
        if (res.data.length < 10) {
          this.finished = true;
        } else {
          this.status ? this.pageNumber2++ : this.pageNumber1++
        }
      }, 500);
    }
  }
}
</script>
<style lang="less" scoped>
/deep/.van-tab__pane {
  padding: 5px 10px;
}

/deep/.van-nav-bar__arrow {
  color: #303030;
}

/deep/.van-nav-bar__title {
  font-size: 17px;
  font-weight: 500;
  color: #333333;
}

/deep/.van-hairline--bottom::after {
  border: none;
}

/deep/.van-tab {
  &.van-tab--active {
    .van-tab__text {
      color: #1F75D9;
    }
  }

  .van-tab__text {
    font-size: 14px;
    font-weight: 500;
    color: #333333;
  }
}

/deep/.van-tabs__line {
  width: 33px;
  height: 2px;
  background-color: #1F75D9;
}

.btn {
  height: 22px;
  background: #FFB22C;
  border-radius: 11px;
  font-size: 12px;
  font-weight: 500;
  color: #FFFFFF;
  line-height: 22px;
  text-align: center;
  margin-top: 13px;
  padding: 0 10px;
}

.fail {
  color: #999999;
  background: #F6F6F6;
}

.success {
  background: #72B54D;
}

.tag {
  width: 60px;
  height: 20px;
  background: rgba(31, 117, 217, .1);
  text-align: center;
  border-radius: 5px;

  span {
    font-size: 12px;
    transform: scale(0.917);
    font-weight: 400;
    color: #1F75D9;
  }
}

.unit {
  font-size: 12px;
  transform: scale(0.917);
  transform-origin: bottom;
  font-weight: 500;
  color: #1577FF;
}
</style>